<template>
    <div class="goods_container">
        <h4 class="text-center">商品管理</h4>
        <el-row type="flex" justify="space-around">
            <el-col :span="18">
                <el-input v-model="keyword" clearable placeholder="请输入搜索关键词" style="width:180px"
                    @keydown.enter.native="searchGoods"></el-input>
                <el-button type="primary" plain @click="searchGoods">搜索</el-button>
                <el-button type="info" plain @click="requestGoodsData">显示所有</el-button>
            </el-col>
            <el-col :span="2">
                <el-button type="success" plain @click="addGoodsVisible = true">添加</el-button>
            </el-col>
        </el-row>
        <goods-table v-bind:goodslist="list" style="margin-bottom:10px"
            @cancelEmdit="requestGoodsData({},'已取消编辑，并刷新数据')"></goods-table>
        <el-pagination :total="total" :page-size="pageSize" layout="total, jumper, prev, pager, next" background
            @current-change="handleCurrentChange">
        </el-pagination>
        <add-goods v-bind:dialogVisible="addGoodsVisible" @close="addGoodsVisible = false"
            @addSuccess="onAddSuccess"></add-goods>
    </div>
</template>

<script>
import AddGoods from '../goods/AddGoods.vue';
import GoodsTable from '../goods/GoodsTable.vue';

export default {
    components: { GoodsTable, AddGoods },
    name: 'MyGoods',
    data() {
        return {
            addGoodsVisible: false,
            currentPage: 1,
            total: 0,
            pageSize: 5,
            keyword: null,
            categoryId: null,
            list: []
        }
    },
    methods: {
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.requestGoodsData({ currentPage: val })
        },
        searchGoods() {
            let keyword = this.keyword
            this.requestGoodsData({ keyword })
        },
        requestGoodsData({ currentPage = 1, pageSize = this.pageSize, keyword = null, categoryId = null }, successMsg = '成功加载数据') {
            console.log(currentPage, pageSize, keyword, categoryId)
            this.$https.get('/product/page', {
            // this.$https.get('/data/test.json', {
                params: {
                    currentPage,
                    pageSize,
                    keyword,
                    categoryId
                }
            })
                .then(function (response) {
                    console.log(response);
                    //状态200时
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: successMsg,
                            type: 'success'
                        })
                        let { list, total } = res.data
                        this.list = list
                        this.total = total
                        console.log(this.list, this.total);
                    } else {
                        this.$message.error(response.data.msg)
                    }
                }.bind(this))
                .catch(function (error) {
                    console.log(error);
                    this.$message.error('出错了')
                }.bind(this))
        },
        onAddSuccess(msg) {
            this.requestGoodsData({}, msg)
        }

    },
    mounted: function () {
        //发送请求，请求商品第一页
        this.requestGoodsData({})
    }
}
</script>

<style lang="less" scoped></style>