<template>
    <div class="goods_container">
        <el-table :data="goodslist" height="500" style="width: 100%" stripe @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column align="center" label="商品名称" width="90">
                <template slot-scope="scope">
                    <span style="margin-left: 10px" v-if="!scope.row.isEdmit">{{ scope.row.productName }}</span>
                    <el-input v-model="scope.row.productName" placeholder="请输入内容" v-else></el-input>
                </template>
            </el-table-column>
            <el-table-column prop="image" align="center" label="图片" width="90">
            </el-table-column>
            <el-table-column align="center" label="分类" width="100">
                <template slot-scope="scope">
                    <span style="margin-left: 10px" v-if="!scope.row.isEdmit">{{ scope.row.categoryName }}</span>
                    <!-- 分类选择组件 -->
                    <category-select v-model="scope.row.categoryId" @onSelectLabel="scope.row.categoryName = $event"
                        :init-select-label="scope.row.categoryName" :placeholder="scope.row.categoryName"
                        v-else></category-select>
                </template>
            </el-table-column>
            <el-table-column align="center" label="价格" width="140">
                <template slot-scope="scope">
                    <span style="margin-left: 10px" v-if="!scope.row.isEdmit">￥{{ scope.row.price.toFixed(2) }}</span>
                    <el-input-number v-model="scope.row.price" size="mini" :min="0" :precision="2" :step="1"
                        controls-position="right" v-else></el-input-number>
                </template>
            </el-table-column>
            <el-table-column prop="stock" align="center" label="库存" width="140">
                <template slot-scope="scope">
                    <span style="margin-left: 10px" v-if="!scope.row.isEdmit">{{ scope.row.stock }}</span>
                    <el-input-number v-model="scope.row.stock" size="mini" :min="0" v-else></el-input-number>
                </template>
            </el-table-column>
            <el-table-column prop="createTime" align="center" label="创建时间" width="120">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.createTime }}</span>
                </template>
            </el-table-column>
            <el-table-column prop="updateTime" align="center" label="最后更改时间" width="120">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.updateTime }}</span>
                </template>
            </el-table-column>
            <el-table-column prop="description" align="center" label="描述" width="130">
                <template slot-scope="scope">
                    <span style="margin-left: 10px" v-if="!scope.row.isEdmit">{{ scope.row.description }}</span>
                    <el-input type="textarea" v-model="scope.row.description" :autosize="{ minRows: 2, maxRows: 4 }"
                        placeholder="请输入商品描述内容" v-else>
                    </el-input>
                </template>
            </el-table-column>
            <el-table-column prop="origin" align="center" label="产地" width="130">
                <template slot-scope="scope">
                    <span style="margin-left: 10px" v-if="!scope.row.isEdmit">{{ scope.row.origin }}</span>
                    <el-input v-model="scope.row.origin" placeholder="请输入生产地址" autocomplete="off" v-else></el-input>
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="140">
                <template slot-scope="scope">
                    <el-button :type="scope.row.isEdmit ? 'danger' : 'success'" round plain size="small"
                        @click="onEmditClick(scope.row)">
                        {{ scope.row.isEdmit ? '完成' : '编辑' }}
                    </el-button>
                    <el-button @click="onCancelEmdit" type="text" size="small" v-if="scope.row.isEdmit">取消</el-button>
                    <el-button @click="handleClick(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
import categorySelect from '../category/CategorySelect.vue'

export default {
    name: 'GoodsTable',
    props: {
        goodslist: {
            type: Array
        }
    },
    components: {
        categorySelect
    },
    data() {
        return {
            multipleSelection: [],
            beforeEdmintData: this.goodslist,
            edmintData: {}
        }
    },
    methods: {
        handleSelectionChange(val) {
            this.multipleSelection = val;
            console.log(val)
        },
        handleClick() {

        },
        putEmdit(req) {
            this.$https.post('/product',req)
            // this.$https.get('/data/test.json',req)
                .then(function (response) {
                    console.log(response);
                    //状态200时
                    const res = response.data
                    if (res.code === 200) {
                        this.$message({
                            message: '成功修改信息',
                            type: 'success'
                        })
                    } else {
                        this.$message.error(res.msg)
                        //刷新数据
                        this.onCancelEmdit()
                    }
                }.bind(this))
                .catch(function (error) {
                    console.log(error);
                    this.$message.error('出错了')
                }.bind(this))

        },
        onEmditClick(row) {
            if (!row.isEdmit) {
                this.$set(row, 'isEdmit', true)
            } else {
                // this.$delete(row, 'isEdmit')
                row.isEdmit = false
            }

            //点击完成按钮时，发送请求
            if (!row.isEdmit) {
                let { productId,
                    productName,
                    description,
                    categoryId = null,
                    categoryName,
                    parentId = null,
                    price,
                    image,
                    origin,
                    stock } = row
                let req = {
                    productId,
                    productName,
                    description,
                    categoryId,
                    categoryName,
                    parentId,
                    price,
                    image,
                    origin,
                    stock
                }
                this.putEmdit(req)
                console.log(req)
            }
            // console.log(row)

        },
        onCancelEmdit() {
            this.$emit('cancelEmdit')
        }

    }
}
</script>

<style lang="less" scoped>
